import {Component, Input, OnInit} from '@angular/core';
import {ActivatedRoute, Router} from "@angular/router";
import {GlobalReferenceService} from "../services/global.reference.service";
import {Sample} from "../models/sample.model";
import {ApiSampleService} from "../services/api.sample.service";
import {ApiLocationService} from "../services/api.location.service";
import {MarkerLocation} from "../models/markerLocation.model";
import {ApiReferenceService} from "../services/api.reference.service";
import {trigger,style,transition,animate,group,state} from '@angular/animations';
import {WEWFactor, WEWFactorClass} from "../models/wew.model";
import {uniqueFilter} from "../services/arrayUtils";
import {ChartEntity} from "../models/chart-entity.model";

@Component({
  selector: 'app-new-wew-bar-chart',
  templateUrl: './new-wew-bar-chart.component.html',
  styleUrls: ['./new-wew-bar-chart.component.css'],
  animations:[
    trigger('mainCardAnim',[
      state('void',style({opacity:0})),
      transition(':enter',[animate('300ms 150ms',style({opacity:1}))]),
      transition(':leave',[animate('150ms', style({opacity:0}))])
    ])
  ]
})
export class NewWewBarChartComponent implements OnInit {

  public id:number;
  public sample:Sample;
  public location:MarkerLocation;
  constructor(public router:ActivatedRoute, public globalRef: GlobalReferenceService, public sampleApi: ApiSampleService, public locationApi: ApiLocationService, public referenceApi: ApiReferenceService) { }

  ngOnInit() {
    this.router.params.subscribe( p =>{
     this.id  = p['id']
      //load sample..
      this.globalRef.setSampleById(this.id);
    });

    let reference;
      //not very nice but subcribe in subscribe :(((
    //Load sample and its calculations
    this.sampleApi.getSample(this.id).subscribe(result =>{
      this.sample = result
      this.locationApi.getById(this.sample.location_id).subscribe(result => {
        this.location = result
       this.referenceApi.getByWatertype(this.location.watertypeKrwId).subscribe(result => {
         reference = result


         //set the data for the barchart based on the referenceID
         this.globalRef.setReferenceById(reference.id);
       });
      });

    });



  }



  public barChartType = 'bar';
  public barChartLegend = false;

  options: any = {
    multiTooltipTemplate: "<%= datasetLabel %> - <%= value %>",
    tooltips:{
      mode: 'index',
      callbacks: {
        label: (tooltipItem, data) => this.customLabel(tooltipItem, data),
      axisPointer:{type:'shadow'},
      backgroundColor:'rgba(0,0,0,0.9)'
    }
    },
    scales: {
      xAxes: [{
        stacked: true,
        gridLines: {
          color: 'rgba(244,66,66,1)'
        }
      }],
      yAxes: [{
        stacked: true,
        ticks: {
          beginAtZero: true,
          max: 10,
          min: 0
        },
        gridLines: {
          color: 'rgba(244,66,66,1)'
        }
      }]
    },

    responsive: true
  }

  public customLabel(tooltipItem, data)
  {


    let label = data.datasets[tooltipItem.datasetIndex].label || '';
    // console.log("tooltip:", tooltipItem)
    // console.log('dataset: ', data.datasets[tooltipItem.datasetIndex])
    if (label) {
      label += ': ';
    }
    label += Math.round(tooltipItem.yLabel * 100) / 100;
    if ((tooltipItem.index + 1 === data.datasets[tooltipItem.datasetIndex].data.length)) {
      return label;
    }else{
      return '';
    }

  }

}
