
import {ApiReferenceService} from "./api.reference.service";
import {ApiSampleService} from "./api.sample.service";
import {CalculationSample, CalculationSampleOrder, SimpleWEWValue, WEWFactor} from "../models/wew.model";
import {WewValueCalc} from "../models/wewValue.model";
import {Injectable, Input} from "@angular/core";
import {Observable} from "rxjs/internal/Observable";
import {ApiWewService} from "./api.wew.service";
import {MaterialPalette, Palette} from "./palette";
import {forEach} from "@angular/router/src/utils/collection";



@Injectable()
export class GlobalReferenceService {


  public apiReferenceServer: ApiReferenceService;
  public apiSampleServer: ApiSampleService;
  public apiWewService: ApiWewService;
  public pallete: Palette;



  //current ReferenceID and refernceList
  private referenceId: number = 0;
  private krwId: number;
  public reference: CalculationSampleOrder[] = new Array();

  private sampleId: number;
  public sample: CalculationSampleOrder[] = new Array();
  public test: string[] = []
  public expected:number = 1;


  public valueMapperRef:Map<number,WewValueCalc> = new Map();

  public valueMapperSample:Map<number,WewValueCalc> = new Map();

  public barChartDataList:any[] = []
  public valueMapperList:Map<number,WewValueCalc>[] = [];

  //global vars for barcharts, any barchart with reference should take this data.
  public barChartData:any[] = [
    {data: [0], label: 'test',stack: 'test'}
    ];
  public barChartLabels:any[] = ['test', 'test2'];
  public barChartColors:any[] = [];
  public barChartColorsList:any[] = [];

  //keep vars for the filter///
  public filter_check: boolean;
  public eutro_checked: boolean;
  public eutro_3_checked: boolean;
  public hydro_checked:boolean;
  public hydro_3_checked: boolean;
  private factors: WEWFactor[] = [];
  public finished:boolean = false;

  //always call before using barchart to set the reference!!
  //this function sets the data for the barchart based on the reference ID
  public setReferenceById(id: number){
    this.finished = false;
    this.krwId = 0;
    if (this.factors.length === 0) {
      this.getWewFactor();
    }
    //if the array is empty or the ids arent the same get new reference
   if(this.reference == null || this.referenceId !== id){
      this.referenceId = id;
      this.setReference();
     }else{
      this.filter(this.reference)
     this.setData();
     //this.calculateComputedValues(this.reference, this.valueMapperRef);


   }

   }

   public clearGraph(){
    this.expected = 1;
    this.finished = false;
    this.barChartDataList.length = 0;
    this.barChartData.length = 0;
    this.valueMapperList.length = 0;
    this.barChartColorsList.length = 0;
   }

   public setReferenceByKrwId(krwId: number){
     this.finished = false;
     this.referenceId = 0;
     this.clearGraph();
     if (this.factors.length === 0) {
       this.getWewFactor();
     }
       this.krwId = krwId;
       this.setReferenceKrw();

   }

   //set this in the highest class ex: app.compontent.
  public setService(service: ApiSampleService, wewService: ApiWewService, refApi: ApiReferenceService){
    this.apiReferenceServer = refApi;
    this.apiWewService = wewService;
    this.pallete = new MaterialPalette().shift();
    this.apiSampleServer = service;
  }

  //sets the reference based on the global reference ID
  //also sets the split values in the subscribe by calling calculateComputedValues()
  private setReference(){
    this.apiReferenceServer.getCalculationBySample(this.referenceId).subscribe(result=>{
      this.reference = this.orderResult(result);
      this.filter(this.reference)
      this.setData()
    });
  }

  private setReferenceKrw(){
    this.apiReferenceServer.getByWatertype(this.krwId).subscribe( result=>{
      this.apiReferenceServer.getCalculationBySample(result.id).subscribe(result =>{
        this.reference = this.orderResult(result);
        this.filter(this.reference)
        this.setDataSplit();
      })
    })

  }

  //calculates the data from the reference or filterd refList
  private calculateComputedValues(data: CalculationSampleOrder[], valueMapper:Map<number,WewValueCalc>)  {
    //set mapper to 0

    const wew_classes = data[0].wewValue.length;
    for (let i = 1; i < wew_classes + 1; i++) {
      valueMapper.set(i, new WewValueCalc(0, 0));
    }

    data.forEach(x => {
      x.wewValue.forEach(x => {
        if (x.value != null){
          const previousValue = valueMapper.get(x.factor_class_id);
          valueMapper.set(x.factor_class_id, new WewValueCalc(previousValue.value + x.value, previousValue.times + 1));
        }
      });
    });

  }
  public setData(){
    let dataRef = this.createData(this.valueMapperRef, 'Reference');
    let dataSample = this.createData(this.valueMapperSample, 'Sample');
    dataSample.forEach(entry => {
      dataRef.push(entry);
    })

    this.barChartData = dataRef;
    this.done();
  }



  //filters the data based on the vars set in the filterbar.
  filter(data: CalculationSampleOrder[]) {

    if (this.eutro_checked === true) {
      if (this.eutro_3_checked === true) {
        console.log("Eutro +3 filter");
        const filter = (data: CalculationSampleOrder[]) => data.filter(calculation => calculation.taxon.eutrofieringBelasting === 3);
        data = filter(data);
      } else {
        console.log("Eutro waarden filter");
        const filter = (data: CalculationSampleOrder[]) => data.filter(calculation => calculation.taxon.eutrofieringBelasting != null);
        data = filter(data);
      }
    }

    if (this.hydro_checked === true) {
      if (this.hydro_3_checked === true) {
        console.log("Hydro +3 filter");
        const filter = (data: CalculationSampleOrder[]) => data.filter(calculation => calculation.taxon.hydrologischeVerstoring && calculation.taxon.morfologischeVerstoring === 3);
        data = filter(data);
      } else {
        console.log("Hydro waarden filter");
        const filter = (data: CalculationSampleOrder[]) => data.filter(calculation => calculation.taxon.hydrologischeVerstoring && calculation.taxon.morfologischeVerstoring != null);
        data = filter(data);
      }
    }
    this.calculateComputedValues(data, this.valueMapperRef); // Used to calculate the values
  }

  public createData(valueMapper:Map<number,WewValueCalc>, type:string){

    let newData:any[]= [];
    let pos = 0;
    this.barChartLabels.length = 0;
    this.factors.forEach( entry =>{
    this.barChartLabels.push(entry.name);
    entry.classes.forEach(clas=>{
      let  data: any[] = [];
      for (let i = 0; i < pos; i++){
      data.push(0);
      }
      let wew = valueMapper.get(clas.id);
      data.push((wew.value / wew.times).toFixed(2));
      newData.push({data: data, label: clas.description, stack: type});
    });
      pos++;
    });
    return newData;

  }

  public createDataSplitRef(valueMapper:Map<number,WewValueCalc>){
    this.barChartDataList.length = 0;
    this.barChartLabels = ['ref'];
    this.factors.forEach( entry =>{
      let newData:any[]= [];
      let newColor:any[] = [];
      let color = 0;
      entry.classes.forEach(clas=>{
        let  data: any[] = [];
        let wew = valueMapper.get(clas.id);
        newColor.push({backgroundColor: this.pallete.colorAt(color)});
        data.push((wew.value / wew.times).toFixed(2));
        newData.push({data: data, label: clas.description});
        color++;

      });
      this.barChartDataList.push(newData);
      this.barChartColorsList.push(newColor);
    });
  }

  doFilter() {
    if(this.reference != null){
      this.filter(this.reference);
      if(this.referenceId!==0){
        this.setData()
      }else {
        this.setDataSplit();
      }

    }
  }

  private getWewFactor() {
    this.apiWewService.getFactors().subscribe(result => {
      this.factors = result;
      this.setColorWew(result);
      this.setColorWew(result);
    });



  }


  private setColorWew(factors: WEWFactor[]){
    let test = 0
    factors.forEach(entry =>{
      let pos = 0;
      entry.classes.forEach( clas =>{
        this.barChartColors.push({backgroundColor: this.pallete.colorAt(pos)})
        pos++;
        test++
      })
    })

  }


  public setSampleById(id: number) {
    this.finished = false;
    if(this.sample == null || this.sampleId !== id){
      this.sampleId = id;
      this.setSample();
    }else{
      this.calculateComputedValues(this.sample, this.valueMapperSample);

    }
  }

  public setSample(){
      this.apiSampleServer.getCalculationBySample(this.sampleId).subscribe(result=>{
        this.sample = this.orderResult(result);
        this.calculateComputedValues(this.sample, this.valueMapperSample);
        this.setData();
      });

  }

  public done(){
    if(this.reference.length != 0 && this.sample.length != 0){
    this.finished = true;
    console.log('done loading!')
    }
  }

  private orderResult(result: CalculationSample) {
    let order_sampleList = [];

    result.taxon.forEach(taxon => {
      let valueList = []
      result.wewValue.forEach( wew =>{
        if(wew.taxon_id == taxon.id) {
          valueList.push(wew)
        }
      })
      order_sampleList.push(new CalculationSampleOrder(1, taxon, valueList ))
    })

    return order_sampleList;
  }


 public addSample(id: number) {
     this.finished = false;
     let sampleMap: Map<number,WewValueCalc> = new Map()

     this.apiSampleServer.getCalculationBySample(id).subscribe(result=>{
      this.calculateComputedValues(this.orderResult(result), sampleMap);
      this.valueMapperList.push(sampleMap);
       this.setDataSplit();
    });
  }
  public addSamples(ids: number[]) {
    this.finished = false;
    this.expected = ids.length;
    ids.forEach( id=>{

      let sampleMap: Map<number, WewValueCalc> = new Map()
      this.apiSampleServer.getCalculationBySample(id).subscribe(result => {
        this.calculateComputedValues(this.orderResult(result), sampleMap);
        this.valueMapperList.push(sampleMap);
        this.setDataSplit();
      });
    });
    }



  private setDataSplit() {
      this.createDataSplitRef(this.valueMapperRef)
      this.createDataSplitSample(this.valueMapperList);

      this.doneSplit();
    }
    private doneSplit(){
    if(this.reference.length > 0 && this.valueMapperList.length >= this.expected){
    this.finished = true;
     console.log('done loading!')
    }

    }

  private createDataSplitSample(valueMapperList: Map<number, WewValueCalc>[]) {
    this.finished = false;
    let name = 1;
    valueMapperList.forEach(map => {
      this.barChartLabels.push(name)
      let pos = 0
      this.factors.forEach( factor => {
        let color = 0;
        let newData:any[]= [];
       // let newColor:any[] = [];
        factor.classes.forEach(clas=>{
          let  data: any[] = [];
          for (let i = 0; i < name; i++){
            data.push(0);
          }
          let wew = map.get(clas.id);
         // newColor.push({backgroundColor: this.pallete.colorAt(color)});
          data.push((wew.value / wew.times).toFixed(2));
          this.barChartColorsList[pos].push({backgroundColor: this.pallete.colorAt(color)});

          let clone = JSON.parse(JSON.stringify(this.barChartDataList[pos]));
          clone.push({data: data, label: clas.description});
          this.barChartDataList[pos] = clone;
          color++;
        });

        pos++;
       // this.barChartDataList[0].push(newData);
      });
      name++;
    });

}
}
