import {Inject, Injectable} from '@angular/core';
import {Http, Response, Headers} from '@angular/http';

import {ApiService} from './api.service';
import {MarkerLocation} from '../models/markerLocation.model';
import {map} from 'rxjs/operators';
import {Observable} from 'rxjs/internal/Observable';

@Injectable()
export class ApiLocationService extends ApiService {
  constructor(@Inject(Http) http: Http) {
    super(http);
  }

  //Retrieve a list of all locations
  public getAllLocations(): Observable<MarkerLocation[]> {
    return this.get('/location/all').pipe(map((markerLocations: Object[]) =>
      markerLocations.map(markerLocation => MarkerLocation.fromJSON(markerLocation))));
  }

  // Retrieve a location by its ID
  public getById(id): Observable<MarkerLocation> {
    return this.get('/location/id/'+id).pipe(map(markerLocation => MarkerLocation.fromJSON(markerLocation)));
  }

  public getByIds(ids:number[]):Observable<MarkerLocation[]> {
    return this.post('/location/ids',ids).pipe(map(markerLocations => markerLocations.map(markerLocation => MarkerLocation.fromJSON(markerLocation))));
  }

  // Retrieve a location by its code
  public getByCode(code:string):Observable<MarkerLocation>{
    return this.get('/location/code/'+code).pipe(map(markerLocation => MarkerLocation.fromJSON(markerLocation)));
  }

  // Save a location to the server
  public save(markerLocation:MarkerLocation):Observable<MarkerLocation>{
    return this.post('/location',markerLocation).pipe(map(markerLocation=>MarkerLocation.fromJSON(markerLocation)));
  }
}
