import 'hammerjs';

import {BrowserModule} from '@angular/platform-browser';
import {BrowserAnimationsModule} from '@angular/platform-browser/animations';
import {NgModule} from '@angular/core';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import {HttpModule, JsonpModule} from '@angular/http';
import {Routes, RouterModule} from '@angular/router';
import {
  MatCardModule,
  MatSliderModule,
  MatSlideToggleModule,
  MatMenuModule,
  MatRadioModule,
  MatIconModule,
  MatDialogModule,
  MatSnackBarModule,
  MatTabsModule,
  MatToolbarModule,
  MatSidenavModule,
  MatListModule,
  MatGridListModule,
  MatInputModule,
  MatButtonModule,
  MatSelectModule,
  MatProgressBarModule,
  MatProgressSpinnerModule,
  MatCheckboxModule,
} from '@angular/material';
import {NgxDatatableModule} from '@swimlane/ngx-datatable';
import {AppComponent} from './app.component';
import {GMapsComponent} from './google-map/results-gmap.component';
import {CreateAccountComponent} from './user/create-account/create-account.component';
import { ChartsModule } from 'ng2-charts';
import {SidenavComponent} from './sidenav/sidenav.component';
import {SampleViewComponent} from './sample/sample-view/sample-view.component';
import {SampleUploadComponent} from './sample/sample-upload/sample-upload.component';
import {SampleLocationTableComponent} from "./sample-location-table/sample-location-table.component";
import {LoginComponent} from './login/login.component';
import {AuthGuard} from "./services/auth.guard";
import { WewUploadComponent } from './wew/wew-upload/wew-upload.component';
import { NgxEchartsModule } from 'ngx-echarts';
import { EditAccountComponent } from './user/edit-account/edit-account.component';
import { TaxonImportStructureComponent } from './taxon/taxon-import-structure/taxon-import-structure.component';
import { TaxonTreeComponent } from './taxon/taxon-tree/taxon-tree.component';
import { TaxonViewComponent } from './taxon/taxon-view/taxon-view.component';
import {WewBarChartComponent} from './wew/wew-bar-chart/wew-bar-chart.component';
import {WaterComponent} from './water/water.component';
import { TaxonManageGroupsComponent,TaxonManageGroupsEditComponent } from './taxon/taxon-manage-groups/taxon-manage-groups.component';
import {ReferenceListComponent} from './reference/reference-list/reference-list.component';
import {ReferenceEditComponent} from './reference/reference-edit/reference-edit.component';
import {LOCALE_ID} from '@angular/core';
import {SampleCompareComponent} from "./sample/sample-compare/sample-compare.component";
import {SampleQualitySheetComponent} from './sample/sample-quality-sheet/sample-quality-sheet.component';
import { HelpComponent } from './help/help.component';
import {MapComponent} from "./map/map.component";
import {AuthInterceptor} from "./services/AuthInterceptor.service";
import {HTTP_INTERCEPTORS} from "@angular/common/http";
import { registerLocaleData} from "@angular/common";
import localeNl from '@angular/common/locales/nl';
import {GlobalReferenceService} from './services/global.reference.service';
import { NewWewBarChartComponent } from './new-wew-bar-chart/new-wew-bar-chart.component';
import { NewSampleCompareComponent } from './sample/new-sample-compare/new-sample-compare.component';
registerLocaleData(localeNl);

const routes:Routes = [
  {path: '', component: MapComponent, data: {depth: 2}},
  {path: 'login', component: LoginComponent},
  {path: 'map', component: MapComponent, data: {depth: 2}, canActivate: [AuthGuard]},
  {path: 'account', component: EditAccountComponent, data: {depth: 2}, canActivate: [AuthGuard]},
  {path: 'results/water/:id', component: WaterComponent, data: {depth: 3}, canActivate: [AuthGuard]},
  {path: 'data', component: SampleLocationTableComponent, data: {depth: 2}, canActivate: [AuthGuard]},
  {path: 'sample/upload', component: SampleUploadComponent, data: {depth: 2}, canActivate: [AuthGuard]},
  {path: 'sample/view/:id', component: SampleViewComponent, data: {depth: 4}, canActivate: [AuthGuard]},
  {path: 'sample/quality/:id', component: SampleQualitySheetComponent, data: {depth: 5}, canActivate: [AuthGuard]},
  {path: 'taxon/view/:id', component: TaxonViewComponent, data: {depth: 3}, canActivate: [AuthGuard]},
  {path: 'sample/compare', component: SampleCompareComponent, data: {depth: 2}, canActivate: [AuthGuard]},
  {path: 'help', data: {depth: 1}, component: HelpComponent},
  /* Admin Routes */
  {path: 'taxon/import', component: TaxonImportStructureComponent, data: {depth: 2}, canActivate: [AuthGuard]},
  {path: 'taxon/group', component: TaxonManageGroupsComponent, data: {depth: 2}, canActivate: [AuthGuard]},
  {path: 'wew/upload', component: WewUploadComponent, data: {depth: 2}, canActivate: [AuthGuard]},
  {path: 'reference/list', component: ReferenceListComponent, data: {depth: 2}, canActivate: [AuthGuard]},
  {path: 'reference/edit/:id', component: ReferenceEditComponent, data: {depth: 2}, canActivate: [AuthGuard]},
  {path: 'admin/create-account', component: CreateAccountComponent, data: {depth: 2}, canActivate: [AuthGuard]},
  /* Directs back to home if route is unknown */
  {path: '**', component: HelpComponent, data: {depth: 1}, canActivate: [AuthGuard]}
];

@NgModule({
  declarations: [
    MapComponent,
    AppComponent,
    CreateAccountComponent,
    GMapsComponent,
    SampleLocationTableComponent,
    SidenavComponent,
    SampleViewComponent,
    SampleUploadComponent,
    WewBarChartComponent,
    LoginComponent,
    EditAccountComponent,
    WewUploadComponent,
    TaxonImportStructureComponent,
    TaxonTreeComponent,
    TaxonViewComponent,
    WaterComponent,
    TaxonManageGroupsComponent,
    TaxonManageGroupsEditComponent,
    ReferenceListComponent,
    ReferenceEditComponent,
    SampleCompareComponent,
    SampleQualitySheetComponent,
    HelpComponent,
    NewWewBarChartComponent,
    NewSampleCompareComponent,
  ],
  entryComponents: [
    TaxonManageGroupsEditComponent
  ],
  imports: [
    ChartsModule,
    NgxEchartsModule,
    ChartsModule,
    MatCardModule,
    MatDialogModule,
    MatRadioModule,
    MatMenuModule,
    MatSnackBarModule,
    MatIconModule,
    MatToolbarModule,
    MatTabsModule,
    MatCheckboxModule,
    MatSliderModule,
    MatSlideToggleModule,
    MatSidenavModule,
    MatListModule,
    MatGridListModule,
    MatInputModule,
    MatButtonModule,
    MatSelectModule,
    MatProgressBarModule,
    MatProgressSpinnerModule,
    BrowserModule,
    FormsModule,
    HttpModule,
    JsonpModule,
    NgxDatatableModule,
    BrowserAnimationsModule,
    ReactiveFormsModule,
    RouterModule.forRoot(routes, {useHash: false}),
  ],
  providers: [GlobalReferenceService, AuthGuard, {provide: LOCALE_ID, useValue: "nl-NL"},
    {
    provide: HTTP_INTERCEPTORS,
    useClass: AuthInterceptor,
    multi: true
  }],
  bootstrap: [AppComponent]
})
export class AppModule {
}
