import {Component, OnInit, ViewChild} from '@angular/core';
import {ApiLocationService} from '../services/api.location.service';
import {ApiWaterschapService} from '../services/api.waterschap.service';
import {Taxon,TaxonGroup} from '../models/taxon.model';
import {ApiTaxonService} from "../services/api.taxon.service";
import {DatatableComponent} from "@swimlane/ngx-datatable";
import {ApiSampleService} from '../services/api.sample.service';
import {Sample} from '../models/sample.model';
import {MarkerLocation} from '../models/markerLocation.model';
import {uniqueFilter} from '../services/arrayUtils';

type TaxonRow = {id:number,name:string,group?:TaxonGroup};
type SampleRow = {id:number, date:Date, location:string, dateAdded:Date};

@Component({
  selector: 'app-sample-Location-table',
  providers: [ApiLocationService, ApiWaterschapService, ApiTaxonService, ApiSampleService],
  templateUrl: './sample-Location-table.component.html',
  styleUrls: ['./sample-Location-table.component.css']
})

export class SampleLocationTableComponent implements OnInit {
  @ViewChild(DatatableComponent) table: DatatableComponent;
  @ViewChild('sampleDateTemplate') sampleDateTemplate;
  @ViewChild('sampleDateAddedTemplate') sampleDateAddedTemplate;
  @ViewChild('sampleButtonTemplate') sampleButtonTemplate;
  @ViewChild('taxonNameTemplate') taxonNameTemplate;
  @ViewChild('taxonButtonTemplate') taxonButtonTemplate;
  @ViewChild('waterCodeTemplate') waterCodeTemplate;
  @ViewChild('waterDescriptionTemplate') waterDescriptionTemplate;
  @ViewChild('waterDetailsTemplate') waterDetailsTemplate;

  samples = [];
  sampleRows = [];
  sampleColumns = [{name:'Locatie',prop:'location',cellTemplate:null}, {name:'Monster datum',prop:'date',cellTemplate:null}, {name:'Datum toegevoegd',prop:'dateAdded',cellTemplate:null}, {name:'Details',prop:'button',cellTemplate:null}];

  locations = [];
  locationRows = [];
  locationColumns = [{name: 'Mp', prop: 'code', cellTemplate:null}, {name: 'Naam', prop: 'description', cellTemplate:null}, {name: 'details', prop: 'button', cellTemplate:null}];

  waterschappen = [];
  waterschapRows = [];
  waterschapColumns = [{name: 'Naam', prop: 'name'}, {name: 'Adres', prop: 'address'}, {name: 'Huisnummer', prop: 'houseNumber'}, {name: 'Postcode', prop: 'zipCode'}, {name: 'Locatie', prop: 'location'}, {name: 'Telefoonnummer', prop: 'phoneNumber'}];

  taxa = [];
  taxonRows = [];
  groupMap:Map<number,TaxonGroup> = new Map();
  taxonColumns = [{name:'Naam', prop:'name', cellTemplate:null}, {name:'Details', prop:'button', cellTemplate:null}];

  constructor(private sampleApi:ApiSampleService, private locationApi:ApiLocationService, private waterschapApi: ApiWaterschapService, private taxonApi: ApiTaxonService) {
    this.loadResources().then(([samples,locations]) => this.toSampleRow(samples,locations));
  }

  ngOnInit() {
    this.sampleApi.getRecent(100).subscribe(samples => {
      this.sampleRows = samples;
      this.samples = samples;
    });

    this.locationApi.getAllLocations().subscribe(locations => {
      this.locationRows = locations;
      this.locations = locations;
    });

    this.waterschapApi.getAll().subscribe(waterschappen => {
      this.waterschapRows = waterschappen;
      this.waterschappen = waterschappen;
    });

    this.taxonApi.getGroups().subscribe(groups => {
      groups.forEach(group => this.groupMap.set(group.id,group));

      this.taxonApi.getAll().subscribe(taxa => {
        this.taxa = taxa;
        this.taxonRows = taxa.map(taxon => this.toTaxonRow(taxon));
      });
    });
  }

  ngAfterViewInit(){
    this.taxonColumns[0].cellTemplate = this.taxonNameTemplate;
    this.taxonColumns[1].cellTemplate = this.taxonButtonTemplate;
    this.locationColumns[0].cellTemplate = this.waterCodeTemplate;
    this.locationColumns[1].cellTemplate = this.waterDescriptionTemplate;
    this.locationColumns[2].cellTemplate = this.waterDetailsTemplate;
    this.sampleColumns[1].cellTemplate = this.sampleDateTemplate;
    this.sampleColumns[2].cellTemplate = this.sampleDateAddedTemplate;
    this.sampleColumns[3].cellTemplate = this.sampleButtonTemplate;
  }

  updateFilter(event) {
    const val = event.target.value.toLowerCase();
    const waterschappenFiltered = this.waterschappen.filter(function(d) {
      return d.name.toLowerCase().indexOf(val) !== -1 || !val;
    });

    const locationFiltered = this.locations.filter(function(d) {
      return d.description.toLowerCase().indexOf(val) !== -1 || d.code.toLowerCase().indexOf(val) !== -1 || !val;
    });

    const taxaFiltered = this.taxa.filter(function(d) {
      return d.name.toLowerCase().indexOf(val) !== -1 || !val;
    });

    const sampleFiltered = this.sampleRows.filter(function(d) {
      return d.location.toLowerCase().indexOf(val) !== -1 || !val;
    });

    this.waterschapRows = waterschappenFiltered;
    this.locationRows = locationFiltered;
    this.taxonRows = taxaFiltered.map(taxon => this.toTaxonRow(taxon));
    this.sampleRows = sampleFiltered;

    this.table.offset = 0;
  }

  private toTaxonRow(taxon: Taxon): TaxonRow {
    let row: TaxonRow = {id:taxon.id,name:taxon.name};
    let group = this.groupMap.get(taxon.groupId);
    if(group)
      row.group = group;
    return row;
  }

  private toSampleRow(samples: Sample[],locations: MarkerLocation[]) {
    let locationIdMap: Map<number, MarkerLocation> = new Map();
    locations.forEach(location => locationIdMap.set(location.id, location));
    this.sampleRows = samples.map(sample =>
      ({id: sample.id, date: sample.date, dateAdded: this.stringToDate(sample.date_added), location: locationIdMap.get(sample.location_id).description}));
  }

  private stringToDate(date) {
    let cuttedDate = date.substring(0, 11);
    cuttedDate += '"';
    return new Date(cuttedDate);
  }

  private async loadResources(): Promise<[Sample[], MarkerLocation[]]>{
    let samples = await this.sampleApi.getRecent(100).toPromise();
    let locationIds = samples.map(sample => sample.location_id).filter(uniqueFilter);
    let locations;

    if(locationIds.length === 0)
      locations = [];
    else
      locations = await this.locationApi.getByIds(locationIds).toPromise();

    return [samples,locations];
  }
}
