import {Inject, Injectable} from '@angular/core';
import {Http, Response, Headers} from '@angular/http';
import {Observable} from 'rxjs';

import {ApiService} from './api.service';
import {Sample} from '../models/sample.model';
import {Taxon} from '../models/taxon.model';
import {CalculationSample, SimpleWEWValue} from '../models/wew.model';
import {map} from 'rxjs/operators';
import {WewValueCalc} from "../models/wewValue.model";


@Injectable()
export class ApiSampleService extends ApiService {
	constructor(@Inject(Http) http: Http) {
		super(http);
	}

	//Retrieve a sample by its id
	public getSample(id: number): Observable<Sample> {
		return this.get('/sample/' + id).pipe(map(sample => Sample.fromJSON(sample)));
	}

	//Save a sample to the server
	public save(sample: Sample): Observable<Sample> {
		return this.post('/sample', [sample]).pipe(map(samples => Sample.fromJSON(samples[0])));
	}

	//Save a list of samples to the server
	public saveMulti(samples: Sample[]): Observable<Sample[]> {
		return this.post('/sample', samples).pipe(map(samples => samples.map(sample => Sample.fromJSON(sample))));
	}

	//Get a list of calculations for the given sample id
	public getCalculationsBySample(sampleId: number):Observable<SimpleWEWValue[]>{
		return this.get('/calculate/sample/'+sampleId).pipe(map(objs => objs.map(obj => {
			let value = new SimpleWEWValue();
			value.factorClassId = obj.factorClassId;
			value.value = obj.computedValue;
			return value;
		})));
	}

	// NIEUWE MANIER ZODAT FILTEREN MOGELIJK IS
	public getCalculationBySample(sampleId: number): Observable<CalculationSample>{
	  return this.get('/calculate/sample2/' + sampleId).pipe(map(obj => {
	    let calculation = new CalculationSample();
	    calculation.id = obj.sample_id;
	    calculation.taxon = obj.taxon;
	    calculation.wewValue = obj.wew_value;
	    return calculation;
    }));
  }



	public getByLocationId(locationId: number): Observable<Sample[]> {
		return this.get('/sample/location/' + locationId).pipe(map(samples => samples.map(sample => Sample.fromJSON(sample))));
	}

	public getByDate(date: string): Observable<Sample[]> {
		return this.get('/sample/date/' + date).pipe(map(samples => samples.map(sample => Sample.fromJSON(sample))));
	}

	public getDistinctYears(): Observable<String[]> {
		return this.get('/sample/getyears');
	}

	public getRecent(count:number):Observable<Sample[]>{
		return this.get('/sample/recent/'+count).pipe(map(samples => samples.map(sample => Sample.fromJSON(sample))));
	}

	public delete(id:number):Observable<boolean>{
		return this.post('/admin/sample/delete',id);
	}
}
