import {ViewChild, Component, HostListener, OnInit, Input} from '@angular/core';
import { Location } from "@angular/common";
import {Router} from "@angular/router";
import { MatSidenav } from "@angular/material";
import {AuthenticationService} from "./services/auth.service";
import {ApiSampleService} from './services/api.sample.service';
import {CalculationSample} from './models/wew.model';
import {trigger, style, transition, animate, keyframes, query, stagger, state, group} from '@angular/animations';
import {WewValueCalc} from "./models/wewValue.model";
import {GlobalReferenceService} from "./services/global.reference.service";
import {ApiWewService} from "./services/api.wew.service";
import {ApiReferenceService} from "./services/api.reference.service";

@Component({
  selector: 'app-root',
  providers: [AuthenticationService, ApiSampleService, GlobalReferenceService, ApiWewService, ApiReferenceService],
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css'],
  animations: [
    trigger("fade", [
      transition("void => *", [
        style({backgroundColor: "#3f51b5", opacity: 0}),
        animate(200)
      ]),
      transition("* => void", [
        animate(100, style({opacity: 0}))
      ])
    ]),
    trigger('routeAnimation', [
      transition('1 => 2, 1 => 3, 1 => 4, 1 => 5, 2 => 3, 2 => 4, 2 => 5, 3 => 4, 3 => 5, 4 => 5', [
        style({ height: '!' }),
        query(':enter', style({ transform: 'translateX(100%)' })),
        query(':enter, :leave', style({ position: 'absolute', top: 0, left: 0, right: 0 })),
        group([
          query(':leave', [
            animate('0.5s cubic-bezier(.35,0,.25,1)', style({ transform: 'translateX(-100%)' })),
          ]),
          query(':enter', animate('0.5s cubic-bezier(.35,0,.25,1)', style({ transform: 'translateX(0)' }))),
        ]),
      ]),
      transition('5 => 4, 5 => 3, 5 => 2, 5 => 1, 4 => 3, 4 => 2, 4 => 1, 3 => 2, 3 => 1, 2 => 1', [
        style({ height: '!' }),
        query(':enter', style({ transform: 'translateX(-100%)' })),
        query(':enter, :leave', style({ position: 'absolute', top: 0, left: 0, right: 0 })),
        group([
          query(':leave', [
            animate('0.5s cubic-bezier(.35,0,.25,1)', style({ transform: 'translateX(100%)' })),
          ]),
          query(':enter', animate('0.5s cubic-bezier(.35,0,.25,1)', style({ transform: 'translateX(0)' }))),
        ]),
      ])
    ])
  ]
})

export class AppComponent implements OnInit {
  @ViewChild('sidenav') sidenav: MatSidenav;
  @Input() filter_check;
  @Input() eutro_checked;
  @Input() eutro_3_checked;
  @Input() hydro_checked;
  @Input() hydro_3_checked;

  sound1 = new Audio("../assets/filter-on.mp3");
  sound2 = new Audio("../assets/filter-off.mp3");
  title = 'Waterscan';
  loggedIn: boolean;

  constructor(public router: Router, private location: Location, public authenticationService: AuthenticationService,
              public sampleApi: ApiSampleService,
              public apiWewService: ApiWewService,
              public globalRef: GlobalReferenceService,
              public referenceApi: ApiReferenceService) {}

  private valueMapper:Map<number,WewValueCalc> = new Map();

  toLogin() {
    this.router.navigate(['/login']);
  }

  toLogout() {
    this.authenticationService.logout();
    this.router.navigate(['/login']);
  }

  ngOnInit() {
    this.globalRef.setService(this.sampleApi, this.apiWewService, this.referenceApi);
    if (!this.authenticationService.isLoggedIn()){
      this.router.navigate(['/login']);
    }
    if (window.innerWidth < 768) {
      this.sidenav.close();
    } else {
      
      this.sidenav.open();
    }
    this.sound1.load();
    this.sound2.load();
  }

  @HostListener('window:resize', ['$event'])
  onResize(event) {
    if (event.target.innerWidth < 768)
      this.sidenav.close();
    else
      this.sidenav.open();
  }

  goBack() {
    this.location.back();
  }

  filter() {
    if(!this.filter_check){
      this.eutro_checked = false;
      this.eutro_3_checked = false;
      this.hydro_checked = false;
      this.hydro_3_checked  = false;
    }
    this.globalRef.filter_check = this.filter_check;
    this.globalRef.eutro_checked = this.eutro_checked;
    this.globalRef.eutro_3_checked = this.eutro_3_checked;
    this.globalRef.hydro_checked = this.hydro_checked;
    this.globalRef.hydro_3_checked  = this.hydro_3_checked;

    this.globalRef.doFilter();
  }

  getDepth(outlet) {
    return outlet.activatedRouteData["depth"];
  }

  toggleSound() {
    if (this.filter_check)
      this.sound1.play();
    else
      this.sound2.play();

    if (!this.filter_check) {
      this.eutro_checked = false;
      this.eutro_3_checked = false;
      this.hydro_checked = false;
      this.hydro_3_checked = false;
    }
  }
}
