import {Component, Input, OnInit} from '@angular/core';
import {WewChartConfig, WewXAxisConfig} from "../../wew/wew-bar-chart/wew-bar-chart.component";
import {WEWFactor} from "../../models/wew.model";
import {ChartEntity} from "../../models/chart-entity.model";
import {GlobalReferenceService} from "../../services/global.reference.service";
import {animate, state, style, transition, trigger} from "@angular/animations";
export type WewChartConfig = {
  entities:ChartEntity[],
  factors?:WEWFactor[],
  barGap?:string,
  barCategoryGap?:string,
  xAxis?:WewXAxisConfig,
  width?:number,
  height?:number
};

@Component({
  selector: 'app-new-sample-compare',
  templateUrl: './new-sample-compare.component.html',
  styleUrls: ['./new-sample-compare.component.css'],
  animations:[
    trigger('mainCardAnim',[
      state('void',style({opacity:0})),
      transition(':enter',[animate('300ms 150ms',style({opacity:1}))]),
      transition(':leave',[animate('150ms', style({opacity:0}))])
    ])
  ]
})

export class NewSampleCompareComponent implements OnInit {


  @Input() settings:any

  constructor(public globalRef: GlobalReferenceService) { }
  public pos:number;
  ngOnInit() {
    this.pos = this.settings.pos;
  }

  public barChartOptions:any = {
    scaleShowVerticalLines: false,
    responsive: true,
    scales: {
      xAxes: [{
        stacked: true,
        gridLines: {
          color: 'rgba(244,66,66,1)'
        }
      }],
      yAxes: [{
        stacked: true,
        ticks: {
          beginAtZero: true,
          max: 1000,
          min: 0
        },
        gridLines: {
          color: 'rgba(244,66,66,1)'
        }
      }]
    },
  };
  public barChartLabels:string[] = ['2006', '2007', '2008', '2009', '2010', '2011', '2012'];
  public barChartData:any[] = [
    {data: [65], label: 'Series A', stack: 'test'},
    {data: [28], label: 'Series B',stack: 'test'},
    {data: [0,28], label: 'Series A', stack: 'te'},
    {data: [0, 28], label: 'Series B',stack: 'te'},
  ];
  public barChartType:string = 'bar';
  public barChartLegend:boolean = false;

  options: any = {
    tooltips:{
      mode: 'index',
      callbacks: {
        label: (tooltipItem, data) => this.customLabel(tooltipItem, data),
        axisPointer:{type:'shadow'},
        backgroundColor:'rgba(0,0,0,0.9)'
      }
    },
    scales: {
      xAxes: [{
        stacked: true,
        gridLines: {
          color: 'rgba(244,66,66,1)'
        }
      }],
      yAxes: [{
        stacked: true,
        ticks: {
          beginAtZero: true,
          max: 10,
          min: 0
        },
        gridLines: {
          color: 'rgba(244,66,66,1)'
        }
      }]
    },

    responsive: true
  }

  public customLabel(tooltipItem, data)
  {


    let label = data.datasets[tooltipItem.datasetIndex].label || '';
    // console.log("tooltip:", tooltipItem)
    // console.log('dataset: ', data.datasets[tooltipItem.datasetIndex])
    if (label) {
      label += ': ';
    }
    label += Math.round(tooltipItem.yLabel * 100) / 100;
    if ((tooltipItem.index + 1 === data.datasets[tooltipItem.datasetIndex].data.length)) {
      return label;
    }else{
      return '';
    }

  }
}
