export class Sample{
	id:number;
	date:Date;
  location_id:number;
	owner_id:number;
	quality:number;
	x_coor:number;
	y_coor:number;
	date_added:Date;
	taxonValues:Map<number/*taxonId*/,number/*value*/>;

	//Maps don't stringify
	public toJSON() {
		let taxonValuesObj = {};
		this.taxonValues.forEach((value,key) => taxonValuesObj[key] = value);

		return {
			id:this.id,
			date:this.date,
      location_id:this.location_id,
			owner_id:this.owner_id,
			quality:this.quality,
			x_coor:this.x_coor,
			y_coor:this.y_coor,
      date_added:this.date_added,
			taxonValues:taxonValuesObj
		};

	}

	public static fromJSON(obj):Sample{
		let sample = new Sample();
		sample.id = obj.id;
		sample.date = obj.date;
		sample.location_id = obj.location_id;
		sample.owner_id = obj.owner_id;
		sample.quality = obj.quality;
		sample.x_coor = obj.x_coor;
		sample.y_coor = obj.y_coor;
		sample.date_added = obj.date_added;
		sample.taxonValues = new Map();
		for(let key in obj.taxonValues){
			sample.taxonValues.set(parseInt(key,10),obj.taxonValues[key]);
		}
		return sample;
	}
}
