import {Component, NgZone, OnInit, ViewChild} from '@angular/core';
import { } from '@types/googlemaps';
import {ApiLocationService} from "../services/api.location.service";
import {ApiWaterschapService} from "../services/api.waterschap.service";
import {ApiWatertypeService} from "../services/api.watertype.service";
import {ApiSampleService} from "../services/api.sample.service";
import {ApiMarkerService} from "../services/api.marker.service";
import {MarkerLocation} from "../models/markerLocation.model";
import {Watertype} from "../models/watertype.model";
import {Waterschap} from "../models/waterschap.model";
import {ActivatedRoute, Router} from "@angular/router";
import {switchMap} from "rxjs/operators";

@Component({
  selector: 'map',
  providers: [ApiLocationService, ApiWatertypeService, ApiWaterschapService, ApiMarkerService, ApiSampleService],
  templateUrl: './map.component.html',
  styleUrls: ['./map.component.css']
})
export class MapComponent implements OnInit {
  @ViewChild('gmap') gmapElement: any;
  map: google.maps.Map;
  location: google.maps.LatLng;
  infoWindow: google.maps.InfoWindow;
  public googleMarkers = [];
  public samples = [];
  public watertypes = [];
  public waterschappen = [];
  public marker = {
    markerLocation: MarkerLocation,
    watertype: Watertype,
    watertypeKrw: Watertype,
    waterschap: Waterschap,
    lastTakenSample: String,
  };
  public filterYears = [];

  public mapProp = {
    center: new google.maps.LatLng(52.250102, 5.4163903),
    zoom: 7.5,
    mapTypeId: google.maps.MapTypeId.TERRAIN
  };

  public filters = {
    waterschapId: '0',
    watertypeId: '0',
    date: '',
  };


  constructor(private apiWatertype: ApiWatertypeService,
              private apiWaterschap: ApiWaterschapService,
              private apiMarker: ApiMarkerService,
              private apiSample: ApiSampleService,
              private route: ActivatedRoute,
              private router: Router,
              private ngZone: NgZone) {
  }

  ngOnInit() {

    this.apiMarker.getAllFilteredMarkers(this.filters).subscribe(markers => {
        this.samples = markers;
        this.filterSamplesByLocation();
        this.loadMarkers(this.samples);
    }, error => console.log(error));

    this.retrieveWaterschappen();
    this.retrieveWatertypes();
    this.retrieveFilterYears();

    this.map = new google.maps.Map(this.gmapElement.nativeElement, this.mapProp);

  }
  private loadMarkers(samples){
    samples.forEach((item) =>{
      this.newMarker(item);
    });
    this.setMapOnAll(this.map);
  }

  private setMapOnAll(map) {
    for (let i = 0; i < this.googleMarkers.length; i++) {
      this.googleMarkers[i].setMap(map);
    }
  }

  public loadFilters(){
    let filterlijst = [];
    this.setMapOnAll(null);

    this.samples.forEach((item) => {
      if(this.filters.waterschapId !== '0' && item.waterschap && item.waterschap.id === this.filters.waterschapId){
        filterlijst.push(item);
      }
      if(this.filters.watertypeId !== '0' && item.watertype && item.watertype.id === this.filters.watertypeId){
        filterlijst.push(item);
      }
      if(this.filters.watertypeId !== '0' && item.watertypeKrw && item.watertypeKrw.id === this.filters.watertypeId){
        filterlijst.push(item);
      }
      if(this.filters.date !== '' && item.lastTakenSample && item.lastTakenSample === this.filters.date){
        filterlijst.push(item);
      }
    });

    if (this.filters.date !== '' || this.filters.watertypeId !== '0' || this.filters.waterschapId !== '0'){
      this.googleMarkers = [];
      filterlijst.forEach((item) => {
        this.newMarker(item);
      });
    }
    this.setMapOnAll(this.map);
  }

  private filterSamplesByLocation(){
    //zo lang er 100 nog wat "markers" worden gepushed vanuit api dit laten staan.
    let nieuweLijst = [];
    nieuweLijst.push(this.samples[0]);

    for (let i=0; i<this.samples.length; i++) {
      if(i>0 && this.samples[i].markerLocation.id !== this.samples[i-1].markerLocation.id){
        nieuweLijst.push(this.samples[i]);
      }
    }
    this.samples = nieuweLijst;
  }
  private newMarker(sample){
    let marker = new google.maps.Marker({
      position: new google.maps.LatLng(sample.markerLocation.latitude, sample.markerLocation.longitude),
      map: null,
      title: 'm' + <string><any>sample.markerLocation.id,
    });

    marker.addListener('click', () => {
      if(this.infoWindow){
        this.infoWindow.close();
      }
      this.newInfoWindow(sample);
      this.infoWindow.open(this.map,marker);
    });

    this.googleMarkers.push(marker);
  }

  public refreshMarkers() {
    this.loadMarkers(this.samples);
    this.filters = {
      waterschapId: '0',
      watertypeId: '0',
      date: '',
    };
  }

  private newInfoWindow(sample){
    this.infoWindow = new google.maps.InfoWindow();
    let content = document.createElement('div');
    let html = "<style>" +
      ".texts{color:blue; margin-top: -12px; font-family: 'Arial';}" +
      "</style> <mat-list >";


    if (sample.markerLocation){
      html = html +
        "<mat-list-item>" +
        "<h3 matLine>" +
        "<b>Locatie</b>" +
        "</h3>" +
        "<p class='texts' matLine>"+ sample.markerLocation.description + "</p>" +
        "</mat-list-item>" +
        "<mat-list-item>" +
        "<h3 matLine>" +
        "<b>Mp</b>" +
        "</h3> " +
        "<p class='texts' matLine>"+sample.markerLocation.code+ "</p>" +
        "</mat-list-item>";
    }
    if (sample.waterschap){
      html = html +
        "<mat-list-item>" +
        "<h3 matLine>" +
        "<b>Waterschap</b>" +
        "</h3>" +
        "<p class='texts' matLine>"+ sample.waterschap.name+ "</p>" +
        "</mat-list-item>";
    }
    if (sample.watertype){
      html = html +
        "<mat-list-item>" +
        "<h3 matLine>" +
        "<b class='haha'>Watertype</b>" +
        "</h3>" +
        "<p class='texts' matLine>" + sample.watertype.name + " (" + sample.watertype.code + ")"  + "</p>" +
        "<p class='texts' matLine>"+sample.watertypeKrw.name + " (" + sample.watertypeKrw.code+ ")" + "</p>" +
        "</mat-list-item>";
    }
    if(sample.lastTakenSample){
      html = html +
        "<mat-list-item>" +
        "<h3 matLine>" +
        "<b>Laatst genomen monster</b>" +
        "</h3>" +
        "<p class='texts' matLine>" + sample.lastTakenSample + "</p>" +
        "</mat-list-item>";
    }
    content.innerHTML = html;

    let button = content.appendChild(document.createElement('input'));
    button.type = 'button';
    button.className = 'mat-raised-button';
    button.value = 'DETAILS';
    button.setAttribute("style","background-color: #3f51b5; color: #FFFFFF; margin-top: 10px;" +
      "float: right;");

    button.addEventListener("click", () => this.ngZone.run(() => {
      this.router.navigate(["/results/water/", sample.markerLocation.id]);
    }));
    this.infoWindow.setContent(content);
  }

  private retrieveFilterYears() {
    this.route.params
      .pipe(switchMap(params => this.apiSample.getDistinctYears()))
      .subscribe(items => {
        this.filterYears = items;
      }, error => console.log(error));
  }
  private retrieveWaterschappen() {
    this.route.params
      .pipe(switchMap(params => this.apiWaterschap.getAll()))
      .subscribe(items => {
        this.waterschappen = items;
      }, error => console.log(error));
  }

  private retrieveWatertypes() {
    this.route.params
      .pipe(switchMap(params => this.apiWatertype.getAll()))
      .subscribe(items => {
        this.watertypes = items;
      }, error => console.log(error));
  }
}
